<template>
  <div class="list von-cascade">
    <label v-for="($index, f) in fields" 
           class="item item-icon-right" 
           @click="showCascadePanel($index)">
      {{ f }}
      <i class="icon ion-ios-arrow-right grey"></i>
      <span class="item-note">{{ value[$index] }}</span>
    </label>
  </div>
</template>
<style lang='scss'>
  .von-cascade {
    padding-left: 16px;
    background: #FFF;

    .item {
      padding-left: 0px;
      .item-note {
        padding-right: 15px;
      }

      &.item-icon-right .icon {
        color: #ccc;
        font-size: 24px;
        right: 0;
      }
    }
  }
</style>
<script>
  import Vue from 'vue'

  const filter = (filters, data) => {
    let options = []

    data.forEach((d) => {
      let r = true
      filters.forEach((f, i) => {
        r = r && f == d[i]
      })
      if (r) {
        let option = d[filters.length]
        if (!!option && options.findIndex((o) => { return o == option }) == -1) {
          options.push(option)
        }
      }
    })

    return options
  }

  export default {
    props: {
      fields: {
        type: Array,
        required: true
      },

      data: {
        type: Array,
        required: true
      },

      value: {
        type: Array,
        required: true
      }
    },

    data() {
      return {
        options: []
      }
    },

    ready() {
      this.options = this.filter()
    },
    
    methods: {
      showCascadePanel(index) {
        let v = this.value, f = this.fields

        if (index > v.length) {
          // $toast('' + f[index]);
          alert('' + f[index - 1])
          return
        }

        let title = f[index]
        let options = this.filter(index)
        // console.log('show cascade panel =>', title, options)

        _cascadePanel
          .show(title, options)
          .then((optionIndex) => {
            if (optionIndex == -1) return

            this.value.splice(index, 1, options[optionIndex])
            this.resetDown(index)
          })
      },

      filter(index) {
        let filters = [];
        for (let i = 0; i < index; i++) filters.push(this.value[i])
        return filter(filters, this.data)
      },

      resetDown(index) {
        let len = index + 1
        this.value.splice(len, this.value.length - len)
      }
    }
  }
</script>



