<template>
  <div class="page has-navbar" v-nav="{title: '', showBackButton: true}">

    <div class="page-content">
      <div class="item item-divider">
        :
      </div>

      <von-checkbox :options="topics" :value.sync="chosenTopics"></von-checkbox>

      <p class="padding">
        chosen topics: {{ chosenTopics }}
      </p>
    </div>
  </div>
</template>
<style lang="scss" scoped>

</style>
<script>
  import {VonCheckbox} from 'vonic'

  export default{
    components: {
      VonCheckbox
    },

    data(){
      return {
        chosenTopics: [],
        topics: ["", "", "", "", ""]
      }
    },

    methods: {
      back() {
        $router.back({path: '/'})
      }
    }
  }
</script>



