<template>
  <div class="page" v-nav="{title: '', showBackButton: true, onBackButtonClick: back}">

    <div class="page-content padding-top">

      <!-- type: text, password, email, tel -->
      <div class="item item-divider">
        :
      </div>
      <von-input type="text" :value.sync="username" placeholder="//" label=""></von-input>
      <von-input type="password" :value.sync="password" placeholder="" label="" style="margin-bottom: 5px;"></von-input>

      <div class="padding">
        <button class="button button-positive button-block"></button>
      </div>

      <div class="item item-divider">
        :
      </div>
      <von-input type="text" :value.sync="username" placeholder="//"></von-input>

    </div>
  </div>
</template>
<style lang="scss" scoped>

</style>
<script>
  import {VonInput} from 'vonic'

  export default{
    components: {
      VonInput
    },

    data(){
      return {
        username: '',
        password: ''
      }
    },

    methods: {
      back() {
        $router.back({path: '/widgets/input'})
      }
    }
  }
</script>



